import { RGBA_ASTC_12x10_Format } from "three";

export class Bar3D  {

    constructor (infoObject) {

        this.info = infoObject;
        this.data = [
            ["Schlipse", 25, "#00BB00", 0.25],
            ["Bananen", 80, "#00AA00", 0.80],
            ["Fußpilz", 2, "#009900", 0.02],
            ["Freibier", 93, "#18a2d0", 0.93],
            ["Sturm der Liebe", 44, "#007700", 0.44],
            ["","", "#006600",0],
            ["","", "#005500",0]
        ];

        this.slices = [0, 2.8, 5.5, 8.2, 11.5, 14.3, 17.5, 20.3, 23.6, 26.8, 30.1, 33.4, 36.7, 40, 43.3, 
            46.6, 49.8, 53.7, 57, 60.8, 64.6, 68.5, 72.3, 76.1, 79.9, 83.8, 87.6, 92, 95.9, 100.2, 104.6, 109, 113.4, 
            117.8, 122.7, 127.1, 132, 136.9, 141.8, 146.8, 151.7, 157.2, 162.1, 167.6, 173, 178.5, 184.5, 190, 196.1, 
            202.1, 208.1, 214.1, 220.7, 227.3, 233.8, 240.4, 247, 254.1, 261.2, 268.9, 276, 283.7, 291.3, 299.5, 307.7, 
            316, 324.2, 333, 342.3, 351, 360.3, 370.2, 379.5, 389.9, 400.3, 410.7, 421.1, 432.6, 443.6, 455.6, 467.6, 
            479.7, 492.3, 505.4, 518.6, 532.3, 546.5, 561.3, 576, 591.9, 607.8, 624.2, 641.2, 658.7, 676.8, 695.4, 715.1, 
            734.9, 755.7, 777.6, 800];

        this.vTriangle = document.getElementById("Tri1");
        this.vTriangle2 = document.getElementById("Tri2");
        this.vTriangle3 = document.getElementById("Tri3");
        this.vTriangle4 = document.getElementById("Tri4");
        this.persp = document.getElementById("persp");
        this.perspD = document.getElementById("perspD");
        this.persp2 = document.getElementById("persp2");
        
        this.fontSize = 11;
        this.positions = [];
        this.colors = [];
        this.createInitialPositions = this.createInitialPositions.bind(this);
        this.createRightPositions = this.createRightPositions.bind(this);
        this.drawRight = this.drawRight.bind(this);
        this.buildString = this.buildString.bind(this);
        this.buildPolygon = this.buildPolygon.bind(this);
        this.front = this.front.bind(this);
        this.top = this.top.bind(this);
        this.depth = this.depth.bind(this);
        this.scale = this.scale.bind(this);
        this.scaleRightToPoint = this.scaleRightToPoint.bind(this);
        this.createBar = this.createBar.bind(this);
        this.render = this.render.bind(this);
        this.mainStr = this.mainStr.bind(this);
        this.draw = this.draw.bind(this);
        this.setWidth = this.setWidth.bind(this);
        this.transformChart = this.transformChart.bind(this);

        this.G = {
            x0:0, x1:540, x2:800,
            y0:0, y1:150, y2:150, y3:300,
            w0:540, w1:340, w0org:400, w1org:400,
            h0:0, h1:1, h2:0,
        }
        this.svgElements = [];
        this.polygonsFront = [];
        this.polygonsDepth = [];
        this.polygonsTop = [];
        this.polygonsFrontStr = [];
        this.polygonsDepthStr = [];
        this.polygonsTopStr = [];

        this.transform = {x:0, y:0, scaleX:1, scaleY:1};

        this.positionsL = [];
        this.positionsLx = [];
        this.createInitialPositions();
        this.positionsR = [];
        this.positionsRx = [];
        this.createRightPositions();
        this.correctRightSide = 0
        //this.draw(true);
        this.setData(this.data);
    }

    setData(data) {
        let newArr = [];
        this.colors = [];
        let colors_temp = [];
        for (let i = 0; i< data.length; i++ ){
            let text = data[i][0];
            let value = data[i][1];
            let col = data[i][2];
            if (text === "" & value === "") continue;
            if (text === "") text = "???";
            if (isNaN(value) || value ==="") value = 0;
            if (value < 0) value = 0;
            if (value > 100) value = 100;
            if (col === "") col = "#777777";
            newArr.push ([text,  data[i][1]+"",  col,  parseFloat(value)/100.0]);
            colors_temp.push(col);
        }
        this.data = newArr;
        this.colors =  colors_temp.reverse();
        this.update ();
    }

    transformChart () {

        let t = this.transform;
        let str = ["translate(", t.x, " ",t.y,") ","scale(", t.scaleX, " ", t.scaleY, ")"].join("");
        this.info.world.setAttributeNS(null,"transform",str);
    
    }

    draw () {
        this.deleteSVGElements();
        this.polygonsFrontStr = [];
        this.polygonsDepthStr = [];
        this.polygonsTopStr = [];

        this.scale();
        //this.drawLeft();
        this.scaleRightToPoint(this.positionsLx[12].x);
       
        for (let m = this.data.length-1; m >= 0; m--) {
            if (this.data[m][0] === "") continue;
            this.createBar (this.data[m], m*6 +4, 4);
        }
       
        this.render();
        this.renderText();
    }

    createBar (obj, pos, dep) {
        let fr_data   = this.front(pos, dep, obj[3]);
        let fr_string = this.buildPolygon(fr_data);
        let de_data   = this.depth(pos, dep, obj[3]);
        let de_string = this.buildPolygon(de_data);
        let ba_data   = this.depth(pos+4, dep, obj[3]);
        let to_data   = this.top(fr_data, de_data, ba_data);
        let to_string = this.buildPolygon(to_data);
        
        this.polygonsFrontStr.push(fr_string);
        this.polygonsDepthStr.push(de_string);
        this.polygonsTopStr.push(to_string);
    }

    render () {
        let xmlns = "http://www.w3.org/2000/svg";
        let i;
        for (i = 0; i<this.polygonsTopStr.length; i++) {
            let topP = document.createElementNS(xmlns, "polygon");
            topP.setAttributeNS(null,"id","to_"+i);
            topP.setAttributeNS(null,"fill","#000000");
            topP.setAttributeNS(null,"points",this.polygonsTopStr[i]);
            topP.setAttributeNS(null,"stroke",this.colors[i]);
            topP.setAttributeNS(null,"stroke-width",0.66);
            topP.setAttributeNS(null,"stroke-miterlimit",4);
            this.info.world.appendChild(topP);
            this.svgElements.push(topP);
       
            let depP = document.createElementNS(xmlns, "polygon");
            depP.setAttributeNS(null,"id","de_"+i);
            depP.setAttributeNS(null,"fill","#000000");
            depP.setAttributeNS(null,"points",this.polygonsDepthStr[i]);
            depP.setAttributeNS(null,"stroke",this.colors[i]);
            depP.setAttributeNS(null,"stroke-width",0.66);
            depP.setAttributeNS(null,"stroke-miterlimit",4);
            this.info.world.appendChild(depP);
            this.svgElements.push(depP);
        
            let frontP = document.createElementNS(xmlns, "polygon");
            frontP.setAttributeNS(null,"id","fr_"+i);
            frontP.setAttributeNS(null,"fill",this.colors[i]);
            frontP.setAttributeNS(null,"points",this.polygonsFrontStr[i]);
            frontP.setAttributeNS(null,"stroke",this.colors[i]);
            frontP.setAttributeNS(null,"stroke-width",0.66);
            frontP.setAttributeNS(null,"stroke-miterlimit",4);
            this.info.world.appendChild(frontP);
            this.svgElements.push(frontP);
        }
    }

     createInitialPositions() {
        this.positions = [];
        let G = this.G;
        let arr = [];
        let scaleX = this.G.w0 / this.G.w0org;
       
        for (let p = this.slices.length-1; p>0; p--) {

            let xpos = this.slices[p] /2.0;
            let f = xpos/G.w0;
            //top
            let newH = G.y2 * f; 
            let d = G.y2 - newH;
            //bottom
            let newH2 = (G.y3-G.y2) * f;
            let d2 = (G.y3-G.y2) - newH2;

            let str = ["M",xpos, " ", d, " ","L", xpos," ", G.y3-d2, " "].join("");
            arr.push(str);

            this.positions.push({x:xpos, y1:d, y2:G.y3-d2} );
            this.positionsL.push({x:xpos, y1:d, y2:G.y3-d2} );
            this.positionsLx.push({x:xpos, y1:d, y2:G.y3-d2} );
        }
        // ----------show slices left: ----------------------
        //this.persp.setAttributeNS(null,"d",arr.join(""));

     }

    scale() {
        let scaleX = this.G.w0 / this.G.w0org;
        for (let v = 0; v < this.positions.length; v++) {
            this.positionsLx[v].x = (this.positions[v].x) * scaleX;
        }
        scaleX = this.G.w1 / this.G.w0org;

        for (let w = 0; w < this.positionsR.length; w++) {
            this.positionsRx[w].x = this.positionsR[w].x * scaleX;
        }
        this.correctRightSide = 800-this.positionsRx[0].x;
    }


    scaleRightToPoint(x) {
        let scaleX = (this.G.x2 -x) / this.G.w0org;
        for (let w = 0; w < this.positionsR.length; w++) {
            this.positionsRx[w].x = this.positionsR[w].x * scaleX;
        }
        this.correctRightSide = 800-this.positionsRx[0].x;

    }

    createRightPositions () {
        let ps = this.positions;
        for (let p = ps.length-1; p>=0; p--) {
            let pos = 800 - ps[p].x;
            this.positionsR.push({x:pos, y1:ps[p].y1, y2:ps[p].y2});
            this.positionsRx.push({x:pos, y1:ps[p].y1, y2:ps[p].y2});
        }
    }

    drawLeft () {
        let arr = [];
        for (let p = this.positionsLx.length-1; p>0; p--) {
            let obj = this.positionsLx[p];
            let str = ["M", obj.x, " ", obj.y1, " ","L", obj.x," ", obj.y2, " "].join("");
            arr.push(str);
        }
        this.persp.setAttributeNS(null,"d",arr.join(""));
    }

    drawRight () {
        let arr = [];
        for (let p = 0; p < this.positionsRx.length; p++) { 
            let obj = this.positionsRx[p];
            let x = obj.x + this.correctRightSide;
            let str = ["M", x, " ", obj.y1, " ","L", x," ", obj.y2, " "].join("");
            arr.push(str);
        }
       this.persp2.setAttributeNS(null,"d",arr.join(""));
    }

    buildPolygon (obj) {
        let str = [
            obj.x1, ",", obj.y1, " ", 
            obj.x2, ",", obj.y2, " ",
            obj.x3, ",", obj.y3, " ",
            obj.x4, ",", obj.y4, " " ].join("");
        return str;
    }

    buildString (obj) {
        let str = [
            "M", obj.x1, " ", obj.y1, " ", 
            "L", obj.x2, " ", obj.y2, " ",
            "L", obj.x3, " ", obj.y3, " ",
            "L", obj.x4, " ", obj.y4, " " ].join("");
        return str;
    }

    front(no, w, value) {
        let fr = {
            x1: this.positionsLx[no].x,
            y1: this.positionsLx[no].y1,
            x2: this.positionsLx[no].x,
            y2: this.positionsLx[no].y2,
            x3: this.positionsLx[no+w].x,
            y3: this.positionsLx[no+w].y2,
            x4: this.positionsLx[no+w].x,
            y4: this.positionsLx[no+w].y1
        };
        let d1 = (fr.y2 - fr.y1) * value;
        fr.y1 = fr.y2 - d1;
        let d2 = (fr.y3 - fr.y4) * value;
        fr.y4 = fr.y3 - d2;
        return fr;
    }

    top (front, depth, back) {
        let to = {
            x1: front.x3,
            y1: front.y4,
            x2: front.x2,
            y2: front.y1,
            x3: depth.x3,
            y3: depth.y4,
            x4: back.x4,
            y4: back.y4 
        };
        return to;
    }

    depth (no, depth, value) {
        let pos = this.positionsRx.length -no - depth; // pos in scaled right positions, with depth
        let line = this.positionsRx[pos]; // the object
        let pos2 = this.positionsRx.length -no; // pos in scaled right positions, no depth
        let line2 = this.positionsRx[pos2]; // the object at no depth
        let x = line.x + this.correctRightSide; // x pos corrected, still offset
        let x2 = line2.x + this.correctRightSide; // x pos corrected, still offset
        let pos3 = this.positionsLx[no].x; // the real position without depth
        let shift = x2-pos3; // shift to left
        let dep = {
            x1: this.positionsLx[no].x,
            y1: this.positionsLx[no].y1,
            x2: this.positionsLx[no].x,
            y2: this.positionsLx[no].y2,
            x3: x - shift, 
            y3: line.y2,
            x4: x - shift,
            y4: line.y1
        };
        let d1 = (dep.y2 - dep.y1) * value;
        dep.y1 = dep.y2 - d1;
        let d2 = (dep.y3 - dep.y4) * value;
        dep.y4 = dep.y3 - d2;
        return dep;
    }

    mainStr(p) {
        let G = this.G;
        switch (p) {
            case "p0":
                return [G.x0, ",", G.y2, " "].join("");
            case "p1":
                return [G.x1, ",", G.y0, " "].join("");
            case "p2":
                return [G.x2, ",", G.y1, " "].join("");
            case "p3":
                return [G.x1, ",", G.y3, " "].join("");
            case "p4":
                return [G.x1, ",", G.y1, " "].join("");
            case "p5":
                return [G.x1, ",", G.y2, " "].join("");
        }
    }

    setCenter (val) {
        this.G.x1 = val;
        this.G.w0 = val - this.G.x0;
        this.G.w1 = this.G.x2 - this.G.x1;
        this.update();  
    }
    setHorizontal (val) {
        this.transform.x = val;
        this.transformChart();
    }
    setVertical (val) {
        this.transform.y = val;
        this.transformChart();
    }
    setScale(val) {
       
        // this.transform.scaleX = val /100.0;
        // this.transform.scaleY = Math.abs(val)/100.0;
        // this.transform.x = 800;
        // this.transform.y = 0;
        // this.transformChart();
    }

    setWidth (val) {
       this.update(val);
    }

    deleteSVGElements () {
        for (let m = this.svgElements.length-1; m >= 0; m--) {
            this.svgElements[m].remove();
        }
    }

    update () {
        this.draw();
    }


    renderText () {
        let xmlns = "http://www.w3.org/2000/svg";
        let x = 580;
        let lineHeight = 28;
        let y = 96 + (this.polygonsFrontStr.length-1) * lineHeight;
        for (let m = 0; m < this.polygonsFrontStr.length;  m++) {
            let tex1 = document.createElementNS(xmlns, "text");
            tex1.setAttributeNS(null,"id","id_"+this.data[m][0]);
            tex1.setAttributeNS(null,"fill","#999999");
            tex1.setAttributeNS(null,"x",x+"px");
            tex1.setAttributeNS(null,"y",(y- m*lineHeight)+"px");
            tex1.setAttributeNS(null,"font-size",this.fontSize+"px");
            tex1.setAttributeNS(null,"font-family","Roboto, Helvetica, sans-serif");
            tex1.textContent = this.data[m][1] ; // percent value
            this.info.agenda.appendChild(tex1);
            this.svgElements.push(tex1);
            
            let tex2 = document.createElementNS(xmlns, "text");
            tex2.setAttributeNS(null,"id","id_"+this.data[m][0]);
            tex2.setAttributeNS(null,"fill",this.data[m][2]);
            tex2.setAttributeNS(null,"x",(x+30)+"px");
            tex2.setAttributeNS(null,"y",(y- m*lineHeight)+"px");
            tex2.setAttributeNS(null,"font-size",this.fontSize+"px");
            tex2.setAttributeNS(null,"font-family","Roboto, Helvetica, sans-serif");
            tex2.textContent = this.data[m][0];
            this.info.agenda.appendChild(tex2);
            this.svgElements.push(tex2);
        }
    }

    setHeadline (value) {
       
        let str = ["translate(", 0, " ", parseInt(value), ")"].join("");
        
        this.info.headline.setAttributeNS(null,"transform",str);
    }


    drawTriangles() {
        // usd for tests
        let triPoints = [this.mainStr("p0"), this.mainStr("p1"),this.mainStr("p5")].join("");     
        this.vTriangle.setAttributeNS(null,"points",triPoints);
        let triPoints2 = [this.mainStr("p0"), this.mainStr("p5"),this.mainStr("p3")].join("");     
        this.vTriangle2.setAttributeNS(null,"points",triPoints2);
        let triPoints3 = [this.mainStr("p1"), this.mainStr("p2"),this.mainStr("p4")].join("");     
        this.vTriangle3.setAttributeNS(null,"points",triPoints3);
        let triPoints4 = [this.mainStr("p2"), this.mainStr("p3"),this.mainStr("p4")].join("");     
        this.vTriangle4.setAttributeNS(null,"points",triPoints4);
    }

}
export default Bar3D 

