<template>

  <div class="app" id="app"
        v-bind:style="{ 
        maxWidth: (appWidth) +'px',
        flexWrap: appWrap
        }">

    <GridApp ref="gridapp" class="grid" v-bind:userData.sync="barsData" v-on:dataChange="handlechangedata"/>

    <div class="tools-wrapper">

        <div class="tool">
            
            <div class="slider-wrapper">
                <div class="slider-label">Zentrum:</div> 
                <input class="slider" type="range" min="100" max="700" step="1" v-model="sliderCenter"> 
            </div>

            <div class="slider-wrapper">
                <div class="slider-label">Links/Rechts:</div> 
                <input class="slider" type="range" min="-300" max="600" step="1" v-model="sliderH"> 
            </div>

            <div class="slider-wrapper">
                <div class="slider-label">Unten/Oben:</div> 
                <input class="slider" type="range" min="-250" max="150" step="1" v-model="sliderV"> 
            </div>

            <div class="slider-wrapper">
                <div class="slider-label">Headline:</div> 
                <input class="slider" type="range" min="-30" max="200" step="1" v-model="sliderHeadline"> 
            </div>

        </div>
    </div>
</div>
   
</template>

<script>

import GridApp from './components/datagrid/GridApp.vue';
import BarChart3D  from './components/charts/BarChart3D.js';


export default {
  name: 'App',
  components: {GridApp},
  props: {
    
  },
  
  data() {
      
      return {
          barsData: [
              ["Schlipse", 25, "#00BB00"],
              ["Bananen", 80, "#00AA00"],
              ["Fußpilz", 2, "#009900"],
              ["Freibier", 93, "#18a2d0"],
              ["Sturm der Liebe", 44, "#007700"],
              ["","", "#006600"],
              ["","", "#005500"],
          ],
          appWidth:800,
          appWrap:"nowrap",
          sliderCenter:542,
          sliderH: 0,
          sliderV:0,
          sliderS:0,
          sliderHeadline:0,
          world3d: new BarChart3D("barChartContent", "chartAgenda", "chartHeadline"),
        }
    },
    methods: {

        handlechangedata (newArr) {
            this.barsData = newArr;
            this.world3d.setData(this.barsData);
        },

        handleWindowResize(e) {
            this.appWidth = Math.min(1120, window.innerWidth * 0.9);    // ugly but: 1120 and 0.9 are set in css elsewhere
            this.appWrap = "nowrap";
            if (this.appWidth <= 600) {
                this.appWrap = "wrap";
            }
            this.$refs.gridapp.handleSize(this.appWidth); 
        }
    },

    watch : {
        sliderCenter:function(val) {
            this.world3d.setWorldCenter(val);
        },
        sliderH:function(val) {
            this.world3d.setHorizontal(val);
        },
        sliderV:function(val) {
            this.world3d.setVertical(val);
        },
        sliderHeadline:function(val) {
            this.world3d.setHeadline(val);
        },
        // sliderS:function(val) {
        //     this.world3d.setScale(val);
        // },
        handleData:function(val) {
        },
        
    },


    mounted: function () {
        window.addEventListener('resize', this.handleWindowResize);
        this.handleWindowResize();
    }
}
</script>

