<template>
    <div class="cell-head-container" v-bind:style="{ width: newWidth +'px'}" v-on:mousedown="mousedown">
        <div class="grab" v-on:mousedown="mousedown"></div>  
        <div class="cell-text unselectable" v-on:mousedown="mousedown">{{item.text}}</div>
        <div class="grab-right" v-on:mousedown="mousedowngrap"></div> 
    </div>
</template>


<script>
export default {
    name:"GridTopCell",
    props: ["item"],
    mousex1: false,
    mousex2: false,
    data: function () {
        let startDrag = false;
        let w = this.item.width;
        return {
            oldWidth: w,
            newWidth: w,
        }
    },
    methods: {

        mousedown:function (e) {
             if (!this.startDrag) {
                 this.$emit('selectcolumn',this.item.colId, this.newWidth);
             }
        },
        mousedowngrap:function (e) {
            this.startDrag = true;
            this.mousex1 = e.clientX;
            document.addEventListener ("mousemove", this.mousemove);
            document.addEventListener ("mouseup", this.mouseup);
        },
        mouseup:function (){
            document.removeEventListener ("mousemove", this.mousemove);
            document.removeEventListener ("mouseup", this.mouseup);
            this.oldWidth = this.newWidth;
            this.startDrag = false;
            this.stopchangesize ();
        },
        mousemove:function (e){
            this.mousex2 = e.clientX;
            this.newWidth = Math.max(this.oldWidth + (this.mousex2 - this.mousex1), 44);
            this.handlechangesize ();
        },
        handlechangesize:function () {
            this.$emit('changesize',this.item.colId, this.newWidth);
        },

        stopchangesize:function () {
            this.$emit('stopchangesize',this.item.colId, this.newWidth);
        }
    } 
  }


</script>

