        border-bottom: 1px solid #bbbbbb;
        min-width: 30px;
        max-height: 24px;
        min-height: 24px;
        white-space: nowrap;
        cursor:pointer;
    }
    
    .grab {
        background:rgba(0, 0, 0, 0.0);
        height:24px;
