    }
    .grab-right {
        background:rgba(0, 0, 0, 0.0);
        height:24px;
        width:12px;
        border-bottom: 1px solid #bbbbbb;
        cursor: col-resize;
    }
    .grab-right:hover {
        background:rgba(0, 0, 0, 0.15);
    }


