import Bar3D from './Bar3D.js';


export class BarChart3D  {

    constructor (domElement, domElement2, headline) {

        this.world = document.getElementById(domElement);
        this.agenda = document.getElementById(domElement2);
        this.headline = document.getElementById(headline);
        this.bars = new Bar3D ({world:this.world, agenda:this.agenda, headline:this.headline});
       
    }

    setData(data) {
        this.bars.setData(data);
    }
    
    setWorldCenter (val) {
        this.bars.setCenter (val);
    }

    setHorizontal (val) {
        this.bars.setHorizontal (val);
    }

    setVertical (val) {
        this.bars.setVertical (val);
    }
    setHeadline (val) {
        this.bars.setHeadline (val);
    }

    setScale (val) {
        this.bars.setScale (val);
    }

    setBarWidth (val) {
        this.barWidth = val;
        this.bars.setWidth (val);
    }

}

export default BarChart3D 


