<template>
   
    <Input ref="theCell" class="cell-active" 
        v-on:input="handleText"
        @keydown.tab ="handleTab($event)"
        @keydown ="handleKeyDown($event)"
        @keyup="handleKey($event)" 
        v-bind:value="text"
        v-bind:style="{ 
            minWidth: w +'px',
            maxWidth: w +'px',
            minHeight: '24px',
            left: (x +28) +'px',
            top: y +'px',
        }"
        v-bind:class="{'noEvents': dragging }"/>

</template>


<script>

export default {
    name:"CellActive",
    props: ["text","w","x","y"],
    data() {
        return {
            dragging:false,
            keyNav: {x:-1000,y:0}
         }
    },
    methods: {
        setText (val) {
            let el = this.$refs.theCell;
            el.value = val;
        },
        handleText (e){
            this.$emit('changeText', e.target.value)
        },

        handleKeyDown(e) {
            switch (e.code) {
                case "ArrowUp": 
                    e.preventDefault();
                    this.keyNav.x = 0;  this.keyNav.y = -1;
                    break;
                case "ArrowDown": 
                    e.preventDefault();
                    this.keyNav.x = 0;  this.keyNav.y = 1;
                    break;
                case "ArrowLeft": 
                    e.preventDefault();
                    this.keyNav.x = -1;  this.keyNav.y = 0;
                    break;
                case "ArrowRight": 
                    e.preventDefault();
                    this.keyNav.x = 1;  this.keyNav.y = 0;
                    break;
                case "Tab": 
                    e.preventDefault();
                    if (e.shiftKey)  {
                        this.keyNav.x = -1;  this.keyNav.y = 0;
                    }
                    else {
                        this.keyNav.x = 1;  this.keyNav.y = 0;
                    }
                    break;
                default: 
                    this.keyNav.x = -1000;  this.keyNav.y = 0;
            }
        },

        handleTab(e) {
            e.preventDefault();
        },
        

        handleKey (e) {
            if (this.keyNav.x > -1000) {
                this.$emit('jumpcell', this.keyNav);
            }
        },
        setFocus () {
            this.$refs.theCell.focus();
        }
    }
  }

</script>

<style scoped>

    .cell-active {
        position:absolute;
        pointer-events: auto;
        border: 2px solid #057609;
        padding-left:3px;
        top:0px;
        left:28px;
        min-height: 24px;
        outline:none;
    }
    .noEvents {
        pointer-events: none;
    }
    
</style>