<template>
    <div class="row">
        <fragment v-for="item in row.data" v-bind:key="item.id" >
            <Cell 
            v-bind:item="item" 
            v-bind:width="row.width" 
            v-on:rightclick="handlerightclick"
            v-on:mousedown="handlemousedown"
            v-on:mouseenter="handlemouseenter"
            v-on:mouseup="handlemouseup"
            /> 
        </fragment>
     </div>
    
</template>


<script>

import Cell from './Cell.vue';
import { Fragment } from 'vue-fragment';

export default {
    name:"GridRow",
    components: {Fragment, Cell},
    props: ["row","widths","positions", "position"],
    methods: {

        handlerightclick:function(point) {
            this.$emit('rightclick', point);
        },
        handlemousedown:function(info) {
            this.$emit('mousedown',info);
        },
        handlemouseenter:function(info) {
            this.$emit('mouseenter',info);
        },
        handlemouseup:function() {
            this.$emit('mouseup');
        }

    }
  }


</script>

<style scoped>

    .row {
        display:flex;
        background:rgb(175, 199, 85);
    }
    .selected {
        background:rgb(187, 187, 187);
    }

</style>