<template>
    <div class="popup-bg" v-on:mouseleave="hidePullDown" @contextmenu.prevent>  
        <div class="popup-item item-normal" v-on:mouseup="mouseup('copy')"  @contextmenu.prevent>Kopieren</div>
        <div class="popup-item item-normal" v-on:mouseup="mouseup('paste')"  @contextmenu.prevent>Einfügen</div> 
        <div class="popup-item item-normal" v-on:mouseup="mouseup('cut')"  @contextmenu.prevent>Ausschneiden</div>
        <div class="popup-item" v-on:mouseup="mouseup('delete')"  @contextmenu.prevent>Löschen</div> 
    </div>

</template>


<script>

export default {
    name:"PopUpMenu",
    props: ["item"],
    methods: {
        mouseup (value) {
            this.$emit('handlepopup', value);
        },
        hidePullDown (value) {
            this.$emit('handlehide');
        }
    }
  }

</script>

