<template>
    <div class="grid-outer-wrapper" 
                    v-bind:style="{ 
                    maxWidth: (componentWidth) +'px',
                    minWidth: (componentMinWidth) +'px'
                    }">

        <GridTopRow v-bind:widths="griddata.widths"
                    v-bind:positions="griddata.positions"
                    v-bind:scroll="scrollLeft" 
                    v-on:changesize="handlecellsize"
                    v-on:stopchangesize="stopchangesize"
                    v-on:selectcolumn="handleselectcolumn"
                    @contextmenu.prevent="handlecontextmenu"
                    />
        
        <div ref="theGrid" class="grid-canvas-container" v-on:scroll.passive='handleScroll'>
            <fragment v-for="item in griddata.rows" v-bind:key="item.id"  >
                <GridRow 
                    v-bind:row="item" 
                    v-bind:widths="griddata.widths"
                    v-bind:positions="griddata.positions"
                    v-on:mousedown="handlemousedown"
                    v-on:mouseenter="handlemouseenter" 
                    v-on:mouseup="handlemouseup"
                    v-on:rightclick="handlerightclick"/> 
 
            </fragment>

            <div id="dragRectTop" shiftY="8" shiftX="0" class="drag-selection-rect-top" v-bind:style="{ 
                    width: (selection.w) +'px',
                    left: (selection.x +28) +'px',
                    top: (selection.y- 5) +'px',
                    }"
                    v-on:mousedown="handlemousedowndrag"
                    v-bind:class="{'noEvents': dragging }"/>

            <div id="dragRectLeft" shiftY="-8" shiftX="0" class="drag-selection-rect-top" v-bind:style="{ 
                    width: (selection.w) +'px',
                    left: (selection.x +28) +'px',
                    top: (selection.y +selection.h) +'px',
                    }"
                    v-on:mousedown="handlemousedowndrag"
                    v-bind:class="{'noEvents': dragging }"/>

            <div id="dragRectRight" shiftY="0" shiftX="-5" class="drag-selection-rect-right" v-bind:style="{ 
                    height: (selection.h) +'px',
                    left: (selection.x +28+ selection.w) +'px',
                    top: (selection.y) +'px',
                    }"
                    v-on:mousedown="handlemousedowndrag"
                    v-bind:class="{'noEvents': dragging }"/>

            <div id="dragRectLeft" shiftY="0" shiftX="5" class="drag-selection-rect-right" v-bind:style="{ 
                    height: (selection.h) +'px',
                    left: (selection.x +28-5) +'px',
                    top: (selection.y) +'px',
                    }"
                    v-on:mousedown="handlemousedowndrag"
                    v-bind:class="{'noEvents': dragging }"/>



            <div class="selection" v-bind:style="{ 
                    width: selection.w +'px',
                    minHeight: selection.h +'px',
                    left: (selection.x +28) +'px',
                    top: selection.y +'px',
                    }"
                    />

            <CellActive ref="activeCell"
                    v-on:jumpcell="jumpcell"  
                    v-on:changeText="handleTextChange"
                    v-bind="{ id:'theInputCell', 'text':newText, 'w':cellWidth, 'x':cellX, 'y':cellY}" 
                    />

             <div class="drag-target-rect" v-bind:style="{ 
                    width: (this.dragTargetRect.w) +'px',
                    minHeight: (this.dragTargetRect.h) +'px',
                    left: (this.dragTargetRect.x +28) +'px',
                    top: (this.dragTargetRect.y) +'px',}"
                    v-bind:class="{'show-me': dragging }"
                    />
               

           
        </div>

         <GridLeftColumn 
            v-bind:scroll="scrollTop" 
            v-on:selectrow="handleselectrow"
         />

         <div class="top-left-corner"  v-on:click="handlerpulldown">  
            <IconArrowDown />
         </div>

         <PopUpMenu 
            v-on:handlepopup="handlepopup" 
            v-on:handlehide="hidePullDown"
            v-bind:style="{ 
                left: (this.pulldown.x) +'px',
                top: (this.pulldown.y) +'px',
                }"
        />

    </div>

</template>


<script>
import Vue from 'vue';
import GridData from './GridData.js';
import GridTopRow from './GridTopRow.vue';
import GridRow from './GridRow.vue';
import GridLeftColumn from './GridLeftColumn.vue';
import CellActive from './CellActive.vue';
import PopUpMenu from './PopUpMenu.vue';
import IconArrowDown from './IconArrowDown.vue';
import { Fragment } from 'vue-fragment';

export default {

    name:"GridApp",
    components: {GridRow,GridTopRow, GridLeftColumn, CellActive, Fragment, PopUpMenu, IconArrowDown},
    props:{
        userData: Array
    },

    data() {

        let gd = new GridData();
        return {
            position:{x:0,y:0}, 
            componentWidth:600,
            componentMinWidth:320,
            griddata:gd,
            scrollLeft:0,
            scrollTop:0,
            selection: {
                r1:false, r2: false, c1:false ,c2:false,
                x:false, y:false, w:false, h:false,
                totalR: false, totalC:false
            },
            normalized: {
                r1:false, r2: false, c1:false ,c2:false,
                x:false, y:false, w:false, h:false,
                totalR: false, totalC:false
            },
            destination: {
                r1:false, r2: false, c1:false ,c2:false,
                x:false, y:false, w:false, h:false,
                totalR: false, totalC:false
            },

            row1:false,
            row2:false,
            firstRow:false,
            rowsSelected:false,
            col1:false,
            col2:false,
            firstCol: false,
            colsSelected:false,
            selectionX:-1000,
            selectionY:0,
            selectionW:0,
            selectionH:24,
            
            mouseIsDown:false,
            cellText:"Hu",
            cellWidth:0,
            cellX:-1000,
            cellY:0,
            editable:"none",
            newText:"new Text",
            copyRange: {row1:-1, row2:-1, col1:-1, col2:-1},
            copy:[],
            dragInfo:{
                x1:0,
                y1:0,
                orgx:0,
                orgy:0,
                dx:0,
                dy:0,
                orgRow:-1,
                orgCol:-1,
                targetRow:-1,
                targetCol:-1
            },
            dragTargetRect:{x:0,y:0,w:0,h:0},
            dragging:false,
            pulldown: {x:-1000, y:24, visible:false}
        }
    },

    created() {
        this.mapUserData();
    },

    destroyed() {
    },

    mounted () {
       // this.mapUserData;
    },


    methods: { 

        mapUserData () {
            for (let i = 0; i < this.userData.length; i++) {
                for (let j = 0; j < this.userData[i].length; j++) {
                    this.griddata.rows[i].data[j].text = this.userData[i][j];
                }
            }
        },

        mapToTemp () {
            let orgUserDataCols = this.userData[0].length;
            let arr = [];
            for (let i = 0; i < this.griddata.rows.length; i++) {
                let row = [];
                for (let j = 0; j < orgUserDataCols; j++) {
                    row.push(this.griddata.rows[i].data[j].text);
                }
                arr.push(row);
            }
            //console.log("mapped:",arr);
            return arr;
        },
        
        convertpageXY (pageX, pageY) {
            return {x:pageX -this.position.x -28, y: pageY -this.position.y};
        },

        setData (data) {
            for (let i = 0; i < data.length; i++) {
                for (let j = 0; j < data[i].length; j++) {
                    this.griddata.rows[i].data[j].text = data[i][j];
                }
            }
            data[0][0] = "Test";
        },

        handlecellsize(id, newWidth) {
          
            this.updateCellsWidth();
            this.activeCellToPosition();
            this.reduceSelection ();
        },

        stopchangesize() {
            this.activeCellToPosition();
        },

        activeCellToPosition () {
            if (this.selection.r1 === false){
                return;
            }
            this.cellX = this.griddata.positions[this.selection.c1];
            this.cellY = this.selection.r1 * 24;
            this.cellText = this.griddata.rows[this.selection.r1].data[this.selection.c1].text;
            this.cellWidth = this.griddata.widths[this.selection.c1];
            this.updateText();
        },

        activeCellToNewWidth () {
            this.cellWidth = this.griddata.widths[this.selection.c1];
        },

        noSelection() {
            this.cellX  = -1000;
            this.setSelectionRect("hide");
        },

        handlemousedown (info) {
            this.selection.r1 = info.row;
            this.selection.r2 = info.row;
            this.selection.c1 = info.column;
            this.selection.c2 = info.column;
            this.selection.x  = this.griddata.positions[info.column];
            this.selection.y  = info.row * 24;
            this.selection.w  = this.griddata.widths[info.column];
            this.selection.h  = 24;
            this.selection.totalR = 1;
            this.selection.totalC = 1;
            this.cellX = this.griddata.positions[info.column];
            this.cellY = info.row * 24;
            this.cellText = info.text;
            this.cellWidth = info.width;
            this.updateText(info.text+"");
            this.mouseIsDown = true;
            this.setSelectionRect();
        },

        handlemouseup () {
            this.mouseIsDown = false;
            this.$refs.activeCell.$el.focus(); 
            this.editable = "auto";
            this.normalizeSelection ();
        },


        getColumnUnderMouse (x) {
            let test = 0;
            for (let i = 0; i< this.griddata.positions.length; i++) {
                if (this.griddata.positions[i] > x) {
                    test = i-2;
                    break;
                }
            }   
            return test;
        },

        handlemousedowndrag (e) {
           
            let pointClicked = this.convertpageXY(e.pageX, e.pageY);
            let shiftX = parseInt(e.target.getAttribute("shiftX"),10);
            let shiftY = parseInt(e.target.getAttribute("shiftY"),10);

            this.normalizeSelection();
            this.dragging = true;
            this.$refs.activeCell.dragging = true;

            this.dragInfo.x1 = pointClicked.x + shiftX;
            this.dragInfo.orgx = e.pageX;
            this.dragInfo.y1 = pointClicked.y + shiftY;
            this.dragInfo.orgy = e.pageY;
            this.dragInfo.orgRow= this.getRowAtY(this.dragInfo.y1);
            this.dragInfo.orgCol= this.getColumnAtX(this.dragInfo.x1);
            
            this.setTargetRect(true);
            document.addEventListener ("mousemove", this.mousemovedrag);
            document.addEventListener ("mouseup", this.mouseupdrag);
        },

        getRowAtY(y) {
            return parseInt(y/24,10);
        },

        getColumnAtX(x) {
            let col = 0;
            for (let i = 0; i< this.griddata.positions.length; i++) {
                if (x <this.griddata.positions[i]) break;
                col = i;
            }
            return col;
        },
    
        finishDragging () {
            this.selection.r1 = this.dragInfo.targetRow;
            this.selection.r2 = this.dragInfo.targetRow + this.selection.totalR -1;
            this.selection.c1 = this.dragInfo.targetCol;
            this.selection.c2 = this.dragInfo.targetCol + this.selection.totalC -1;
            this.setSelectionRect();
            this.activeCellToPosition();
            this.paste();
            this.updateText();
            this.$refs.activeCell.$el.focus(); 
        },

        mouseupdrag (e) {
            this.copyToInternalClipboard();
            this.delete(); 
            this.dragging = false;
            this.$refs.activeCell.dragging = false;
            document.removeEventListener ("mousemove", this.mousemovedrag);
            document.removeEventListener ("mouseup", this.mouseupdrag);
            this.finishDragging();
        },

        setTargetRect (fromOrg) {
            if (fromOrg) {
                this.dragTargetRect.x = this.normalized.x;
                this.dragTargetRect.y = this.normalized.y;
                this.dragTargetRect.w = this.normalized.w;
                this.dragTargetRect.h = this.normalized.h;
            }
            else {
                // mouse is now on this column / row:
                let currentCol = this.getColumnAtX(this.dragInfo.x1 + this.dragInfo.dx);
                let currentRow = this.getRowAtY(this.dragInfo.y1 + this.dragInfo.dy);

                // difference
                let dCol = currentCol - this.dragInfo.orgCol;
                let dRow = currentRow - this.dragInfo.orgRow;

                //targets (from org start row and column)
                this.dragInfo.targetCol = this.selection.c1 + dCol;
                this.dragInfo.targetRow = this.selection.r1 + dRow;
                
                this.dragTargetRect.x = this.griddata.positions[this.dragInfo.targetCol];
                this.dragTargetRect.y = this.dragInfo.targetRow *24;
                this.dragTargetRect.w = this.getWidthFromTo(this.dragInfo.targetCol, this.dragInfo.targetCol + (this.selection.totalC -1));
                this.dragTargetRect.h = this.selection.totalR *24;   
                }
        },

        getWidthFromTo (from,to) {
            let w = 0;
                for (let i = from; i<=to; i++){
                    w+= this.griddata.widths[i];
                }
           return w;
        },

        mousemovedrag (e) {
            this.dragInfo.dx = e.pageX -this.dragInfo.orgx; 
            this.dragInfo.dy = e.pageY -this.dragInfo.orgy;
            this.setTargetRect();
        },

        handlemouseenter (info) { // info comes from underlying cell
            if (this.mouseIsDown) {
                this.selection.r2 = info.row;
                this.selection.c2 = info.column;
                this.selection.totalR = Math.abs(this.selection.r2 - this.selection.r1) +1;
                this.selection.totalC = Math.abs(this.selection.c2 - this.selection.c1) +1;
                this.setSelectionRect();
            }
            else {
                if (this.dragging) {
                    this.dragInfo.targetRow= info.row;
                    this.dragInfo.targetCol= info.column;
                }
            }
        },

        handlerightclick:function(point) {
            //onolet 
            event.preventDefault();
            console.log(point, this.position.x);
            let x = point.x - this.position.x;
            let y = point.y -  this.position.y;

            //this.showPullDown(this.position.x-12, this.position.y-12);
            this.showPullDown(point.x-this.position.x-5,30);      

            
        },

        setSelectionRect (mode) {
            if (mode && mode === "hide") {
                this.selection.x =-1000;
                this.selection.w =-1;
                return;
            }
            let x1 = this.griddata.positions[this.selection.c1];
            let x2 = this.griddata.positions[this.selection.c2];
            let y1 = this.selection.r1 *24;
            let y2 = this.selection.r2 *24;

            if (x1 < x2) {
                this.selection.x = x1;
                this.selection.w = this.griddata.positions[this.selection.c2+1]-x1;
            }
            else {
                this.selection.x = x2;
                this.selection.w = x1-x2+ this.griddata.widths[this.selection.c1];
            }
            if (y1 < y2) {
                this.selection.y = y1;
                this.selection.h = y2-y1 +24;
            }
            else {
                this.selection.y = y2;
                this.selection.h = y1-y2 +24;
            }
        },

        normalizeSelection () {
            this.normalized = {...this.selection};
            if ( this.normalized.r1 > this.normalized.r2) {
                let temp =  this.normalized.r1;
                this.normalized.r1 =  this.normalized.r2;
                this.normalized.r2 = temp;
            }
            if (this.normalized.c1 > this.normalized.c2) {
                let temp2 = this.normalized.c1;
                this.normalized.c1 = this.normalized.c2;
                this.normalized.c2 = temp2;
            }
        },

        reduceSelection () {
            this.selection.c2 = this.selection.c1;
            this.selection.r2 = this.selection.r1;
            this.setSelectionRect();
        },

        handleselectcolumn (id) {
            if (id>this.griddata.rows[0].data.length-1) return;
            this.selection.c1 = id;
            this.selection.c2 = id;
            this.selection.r1 = 0;
            this.selection.r2 = this.griddata.rows.length-1;
            this.selection.totalC = 1;
            this.selection.totalR = this.griddata.rows.length;
            this.setSelectionRect();
            this.activeCellToPosition ();
           
            this.$refs.activeCell.$el.focus();
        },

        handleselectrow (id) {
            this.selection.c1 = 0;
            this.selection.c2 = this.griddata.rows[0].data.length-1;
            this.selection.r1 = id;
            this.selection.r2 = id;
            this.selection.totalC = this.griddata.rows[0].data.length;
            this.selection.totalR = 1;
             console.log("in handleselectrow", this.selection);
            
            this.setSelectionRect();
            this.activeCellToPosition();
        },

        handleenterself () {
        },

        updateCellsWidth () {
            for (let j = 0; j < this.griddata.rows.length; j++){
                for (let i = 0; i < this.griddata.rows[j].data.length; i++){
                    this.griddata.rows[j].data[i].width = this.griddata.widths[i];
               }
            }
        },

        getXPosition (col) {
            let position = 0;
            for (let c = 0; c < col; c++){
                position += this.griddata.widths[c]
            }
            return position;
        },

        handleScroll (e) {
            this.scrollLeft = e.target.scrollLeft;
            this.scrollTop = e.target.scrollTop;
            this.handleGridPosition();
        },

        updateText (val) {
            this.newText = this.griddata.rows[this.selection.r1].data[ this.selection.c1].text;
            this.$refs.activeCell.setText(this.newText);
            this.$refs.activeCell.$el.focus(); 
        },

        handleTextChange (value){
            this.griddata.rows[this.selection.r1].data[ this.selection.c1].text = value;
            this.handleChange();
            
        },

        handleChange() {
            this.$emit('dataChange', this.mapToTemp());
        },

        getNewText () {
            return this.cellText;
        },

        jumpcell (navObj) {
            
            let maxRows = this.griddata.rows.length;
            let maxCols = this.griddata.rows[0].data.length;
            let newRow = Math.min(maxRows-1, Math.max(0, this.selection.r1+navObj.y));
            let newCol = Math.min(maxCols-1 , Math.max(0, this.selection.c1+navObj.x));
            let newText = this.griddata.rows[newRow].data[newCol].text;
            let newWidth = this.griddata.widths[newRow];
            let newInfo = {column:newCol, row:newRow, text:newText, width:newWidth};
            this.handlemousedown (newInfo);
            this.activeCellToPosition();
            this.mouseIsDown = false;
        },

        copyToInternalClipboard () {
            this.normalizeSelection();
            this.copy = [];
            for (let i = this.normalized.r1; i <= this.normalized.r2; i++){
                let newRow = [];
                for (let j = this.normalized.c1; j <= this.normalized.c2; j++){
                    newRow.push (this.griddata.rows[i].data[j].text);
                }
            this.copy.push(newRow)   
            }
        },

        delete () {
            this.normalizeSelection();
            for (let i = this.normalized.r1; i <= this.normalized.r2; i++) {
                for (let j = this.normalized.c1; j <= this.normalized.c2; j++) {
                    this.griddata.rows[i].data[j].text = "";
                }
            }
            this.noSelection();
            this.handleChange();
        },

        handlerpulldown (e) {
            this.showPullDown(0,24);
        },

        showPullDown (xpos, ypos) {
           
            if (this.pulldown.visible) {
                this.pulldown.x = -1000;
            } 
            else {
                this.pulldown.x = xpos;
                this.pulldown.y = ypos;
                this.pulldown.visible = false;
            }
            this.pulldown.visible = !this.pulldown.visible
        },

        hidePullDown () {
            this.pulldown.visible = true;
            this.showPullDown(0,24); 
        },

        paste () {
            if (this.copy.length > 0) {
                let startRow = this.selection.r1;
                let endRow = Math.min(this.selection.r1 + this.copy.length, this.griddata.rows.length);
                let startCol = this.selection.c1;
                let endCol = Math.min(this.selection.c1 + this.copy[0].length, this.griddata.rows[0].data.length) ;
                let i,j,m,n;
                for (i = startRow, m = 0; i < endRow; i++, m++) {
                    for (j = startCol, n = 0; j < endCol; j++, n++) {
                        this.griddata.rows[i].data[j].text = this.copy[m][n];
                    }
                }
            }
            this.updateText();
            this.handleChange();
        },

        handlepopup(value) {

            if (this.selection.r1 === false) {
                this.showPullDown(0,24);
                return;
            }
            if (value === "copy") {
               this.copyToInternalClipboard();
            }
            if (value === "paste") {
                this.paste();
            }
            if (value === "cut") {
                this.copyToInternalClipboard();
                this.delete(); 
            }
            if (value === "delete") {
                this.delete(); 
            }
            this.showPullDown(0,24);
        },

        handleSize (newWidth) {
            this.componentWidth = 800;
            if (newWidth <= 600) {
                this.componentMinWidth = newWidth;
            }
            else {
                this.componentMinWidth = 260;
            }
            this.handleGridPosition();
        },

        handleGridPosition () {
            let rect = this.$refs.theGrid.getBoundingClientRect();
            this.position.x = rect.left - this.scrollLeft;
            this.position.y = rect.top - this.scrollTop;
        }

    }

}

</script>

<style scoped>

    .grid-canvas-container {
        display: block;
        position:relative;
        padding-left:28px;
        overflow: scroll;
        max-height: 200px;
        min-height: 100px;
    }
    
    .grid-outer-wrapper {
        display: block;
        position:relative;
        overflow: hidden;
        max-width: 600px;
        min-width: 400px;
        
        border: 1px solid #bbbbbb;
    }

    .top-left-corner {
        position:absolute;
        top:0px;
        left:0px;
        background:rgb(230, 231, 238);
        border-bottom: 1px solid #bbbbbb;
        min-width: 27px;
        max-width: 27px;
        min-height: 24px;
        max-height: 24px;
    }
    .top-left-corner:hover {
        background:rgb(193, 194, 199);
    }

    .selection {
        position:absolute;
        pointer-events: none;
        top:-1000px;
        left:28px;
        background:rgba(7, 116, 16, 0.3);
        min-width: 28px;
        min-height: 24px;
    }

    .drag-selection-rect-top{
        position:absolute;
        top:0px;
        left:28px;
        background:rgba(2, 145, 14, 0);
        min-width: 28px;
        min-height: 5px;
        cursor:grab;
    }
    .drag-selection-rect-right{
        position:absolute;
        top:0px;
        left:28px;
        background:rgba(2, 145, 14, 0);
        min-height: 24px;
        min-width:5px;
        cursor:grab;
    }

    .noEvents {
        pointer-events: none;
    }

    .drag-selection-rect:hover {
        background:rgb(2, 23, 145);
    }

    .drag-target-rect{
        display:none;
        pointer-events: none;
        position:absolute;
        top:0px;
        left:28px;
        border: 2px dashed red;
        min-width: 28px;
        min-height: 5px;
    }

    .show-me{
        display:block;
    }

    
</style>