<template>
    <div class="column-left" v-bind:style="{top: -scroll+24 +'px'}">
        <fragment v-for="item in columnContent" v-bind:key="item.id"  >
            <GridLeftCell 
            v-bind:item="item"
            v-on:selectrow="handleselectrow"/> 
        </fragment>
     </div>
</template>

<script>

import GridLeftCell from './GridLeftCell.vue';
import { Fragment } from 'vue-fragment';

export default {
    name:"GridLeftColumn",
    components: {Fragment, GridLeftCell},
    props: ["scroll"],
    methods: {
        createCellData (numberOfRows) {
            let content = [];
            for (let i = 0; i < numberOfRows; i++) {
                content.push(
                    {
                        id: i+"_LeftRow",
                        rowId: i,
                        text: (i +1) +"",
                    }
                )
            }
            return content;
        },
        handleselectrow (id) {
            this.$emit('selectrow',id);
        }
    },
    data() {
        let data = this.createCellData(10);
        return {
            columnContent:data
        }
    }
}

</script>

<style scoped>

    .column-left {
        display: flex;
        position:absolute;
        top: 24px;
        left:0px;
        flex-direction: column;
        max-width: 28px;
        background:rgb(108, 50, 216);
    }


</style>